
import { useInjection } from '../../../../../../ioc.react';
import { UserService } from "../../../../../../services/user_service";
import User from "../../../../../../models/User";
import { ErrorMessage, Field, Form, Formik, useFormik, validateYupSchema } from 'formik';
import { Link } from 'react-router-dom';
import { AlertService } from '../../../../../../services/alert_service';
import { AuthService } from '../../../../../../services/auth_service';
import * as Yup from 'yup'
import { useEffect } from 'react';





const ContentProfile = () => {


    const user_service = useInjection(UserService);
    const alert_service = useInjection(AlertService);
    const auth_service = useInjection(AuthService);

    function goto() {
        // $('html , body').animate({
        //     scrollTop: $("#btnId").offset()!.top - 20

        // }, 500);

    }

    const initialValues: User = {
        id: "",
        name: "",
        username: "",
        gender: "",
        age: "",
        phoneNumber: "",
        photoUrl: "",
        city: "",
        address: "",
        lastActive: "",
        photos: undefined,
        bankCards: undefined,
    }

    const validateYupSchema = Yup.object().shape({

        name: Yup.string()
            .min(2, 'too short')
            .max(20, 'too long')
            .required('required'),

        username: Yup.string().email('invalid email format'),
        city:Yup.string(),
        phoneNumber:Yup.number().min(11,'11 character').max(11,'11 character')


    })

    useEffect(() => {

        user_service.getUser("2ab3c7d1-a3ff-46df-b045-54c5735a1c00")
            .then(resp => {
                console.log(resp)
                alert_service.success(resp.name, "")
            })
            .catch(error => {
                console.log(error.message)
                alert_service.error(error, "")
            })

    })

    const formSubmitHandler = (values: User) => {



        // if(true){
        //     return confirm("are you sure?")
        // }

        // return true;
        // user_service.getUsers().then(resp=>{console.log(resp)}).catch(err=>{console.log(err)});

        user_service.insertUser(values).then(resp => {
            console.log(resp);
            alert_service.success("اطلاعات با موفقیت ارسال شدند", "kkkk")
        })
            .catch(error => {
                console.log(error.message)
                alert_service.error(error.message, "error");
            })

    }

    return (
        <section className="content">
            <div className="container-fluid">
                <div className="row">
                    <div className="col-md-3">
                        {/* Profile Image */}
                        <div className="card card-primary card-outline">
                            <div className="card-body box-profile">
                                <div className="text-center">
                                    <img
                                        className="profile-user-img img-fluid img-circle"
                                        src={""}
                                        alt="User profile picture"
                                    />
                                </div>
                                <h3 className="profile-username text-center">{""}</h3>
                                <p className="text-muted text-center">{""}</p>
                                <ul className="list-group list-group-unbordered mb-3">
                                    <li className="list-group-item">
                                        <b>آخرین ورود</b> <a className="float-right">{""}</a>
                                    </li>
                                </ul>
                                <a href="#" className="btn btn-primary btn-block">
                                    <b>دنبال کردن</b>
                                </a>
                            </div>
                            {/* /.card-body */}
                        </div>
                    </div>
                    {/* /.col */}
                    <div className="col-md-9">
                        <div className="card">
                            <div className="card-header p-2">
                                <ul className="nav nav-pills">
                                    <li className="nav-item">
                                        <a
                                            className="nav-link active"
                                            href="#prof_pic"
                                            data-toggle="tab"
                                        >
                                            عکس پروفایل                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link" href="#profile" data-toggle="tab">
                                            مشخصات کاربری
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link" href="#reset_passowrd" data-toggle="tab">
                                            بازیابی رمز عبور
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link" href="#bankcard" data-toggle="tab">
                                            کارت بانکی                                        </a>
                                    </li>
                                </ul>
                            </div>
                            {/* /.card-header */}
                            <div className="card-body">
                                <div className="tab-content">
                                    <div className="active tab-pane" id="prof_pic">
                                        <p>عکس کاربر</p>
                                    </div>


                                    <div className="tab-pane" id="profile">
                                        {/* The  profile*/}
                                        <div className="card-header">
                                            <h3 className="card-title">اطلاعات شخصی</h3>
                                        </div>
                                        {/* /.card-header */}
                                        {/* form start */}
                                        <Formik initialValues={initialValues} onSubmit={formSubmitHandler} validationSchema={validateYupSchema} >
                                            {({ dirty, isSubmitting }) =>

                                                <Form role="form">

                                                    {dirty ? <div className="alert alert-warning">
                                                        <h6>هشدار!</h6>
                                                        <p style={{ backgroundColor: "white !important" }}> شما تغییری در اطلاعات انجام داده اید .لطفا  تغییرات را
                                                            <Link to="#" style={{ textDecorationLine: "none", color: " !important" }} onClick={goto}> ذخیره </Link>  نمایید. </p>
                                                    </div> : null}

                                                    <div className="card-body">
                                                        <div className="form-group">
                                                            <label htmlFor="name">نام</label>
                                                            <Field
                                                                type="text"
                                                                className="form-control"
                                                                id="name"
                                                                name="name"
                                                                placeholder="نام خود  را وارد کنید"
                                                            />
                                                            <ErrorMessage name='name' className='error'/>
                                                        </div>
                                                        <div className="form-group">
                                                            <label htmlFor="phoneNumber">شماره موبایل</label>
                                                            <Field
                                                                type="text"
                                                                className="form-control"
                                                                id="phoneNumber"
                                                                name="phoneNumber"
                                                                placeholder="شماره موبایل را وارد کنید"

                                                            />
                                                            <ErrorMessage name='phoneNumber' />

                                                        </div>


                                                        <div className="form-group">
                                                            <label>جنسیت</label>
                                                            <select className="form-control select2 select2-hidden-accessible" style={{ width: "100%" }} aria-hidden="true">
                                                                <option value="male">مرد</option>
                                                                <option value="female">زن</option>
                                                            </select>

                                                        </div>
                                                        <div className="form-group">
                                                            <label htmlFor="city">شهر</label>
                                                            <Field
                                                                type="text"
                                                                className="form-control"
                                                                id="city"
                                                                name="city"
                                                                placeholder=" شهرخود را وارد کنید"
                                                            />
                                                        </div>
                                                        <div className="form-group">
                                                            <label>آدرس</label>
                                                            <textarea
                                                                className="form-control"
                                                                rows={3}
                                                                placeholder="وارد کردن آدرس ..."

                                                            />
                                                        </div>



                                                    </div>
                                                    <div className="card-footer" id="btnId">
                                                        <button type="submit" disabled={!dirty} className="btn btn-primary">
                                                            ارسال
                                                        </button>
                                                    </div>
                                                </Form>
                                            }
                                        </Formik>
                                    </div>
                                    {/* /.tab-pane */}
                                    <div className="tab-pane" id="reset_passowrd">
                                        <form role="form">
                                            <div className="form-group">
                                                <label htmlFor="oldpassword">رمز عبور قبلی</label>
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id="oldpassword"
                                                    name="oldpassword"
                                                    placeholder="پسورد قبلی خود  را وارد کنید"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="password">رمز عبور جدید</label>
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id="oldpassword"
                                                    name="oldpassword"
                                                    placeholder="پسورد جدید خود  را وارد کنید"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="repeatpassword">
                                                    تکرار رمز عبور  جدید
                                                </label>
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id="repeatpassword"
                                                    name="repetpassword"
                                                    placeholder="تکرار رمز عبور  جدید خود  را وارد کنید"
                                                />
                                            </div>
                                            <button type="submit" className="btn btn-primary">
                                                ارسال
                                            </button>
                                        </form>
                                    </div>
                                    <div className="tab-pane" id="bankcard">
                                        <div className="alert alert-info">
                                            <h6>توجه!</h6>
                                            <p>الزاما باید حساب بانکی شخصی که مدارک ایشان ارسال شده ثبت شود </p>
                                        </div>
                                        <div className="alert alert-warning">
                                            <h6>هشدار!</h6>
                                            <p>هیچ گونه مسئولیتی را در قبال واریز  وجه به حساب دیگران را نمیپذبرد </p>
                                        </div>
                                        <form role="form">
                                            <div className="form-group">
                                                <label htmlFor="bankname">نام بانک</label>
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id="bankname"
                                                    name="bankname"
                                                    placeholder="نام بانک را وارد نمایید"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="cardowner">نام صاحب کارت</label>
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id="cardowner"
                                                    name="cardowner"
                                                    placeholder="نام صاحب کارت بانکی را وارد نمایید"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="shaba">شماره شبا</label>
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id="shaba"
                                                    name="shaba"
                                                    placeholder="شماره شبای خود را  وارد نمایید"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="accountnumber">شماره حساب</label>
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id="accountnumber"
                                                    name="accountnumber"
                                                    placeholder="شماره حساب  خود را  وارد نمایید"
                                                />
                                            </div>
                                            <button type="submit" className="btn btn-primary">
                                                ارسال
                                            </button>
                                        </form>
                                    </div>
                                    {/* /.tab-pane */}
                                </div>
                                {/* /.tab-content */}
                            </div>
                            {/* /.card-body */}
                        </div>
                        {/* /.nav-tabs-custom */}
                    </div>
                    {/* /.col */}
                </div>
                {/* /.row */}
            </div>
            {/* /.container-fluid */}
        </section>

    )
}

export default ContentProfile;